.detalle{
  background-color: rgba(0, 0, 0, 0.219);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  display: none;
  justify-content: center;
  align-items: center;
}

.detalle-content {
  background-color: #ddd;
  padding: 10px;
  width: 300px;
  height: 400px;
}
.detalle-open {
  display: grid;
}

